/*РЕЗЕТ*/


* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-weight: 200;
  list-style-type: none;
  outline: none;
  outline: 0;
  outline-offset: 0;
  box-sizing: border-box;
  border: none;
  width: auto;
  height: auto; }



html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  outline: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1;
  overflow-x: hidden;
  width: 100%;
  text-align: center; }

ol,
ul {
  list-style: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

input,
textarea,
select,
button {
  border: none;
  outline: none;
  outline-offset: none;
  background: none;
  border-radius: 0; }

*:active,
*:hover,
*:focus {
  outline: 0;
  outline-offset: none; }








