.width-wrapper {
    width: 1200px;
    text-align: center;
    display: inline-block;
    margin: 0 auto;
    @media (max-width: 1300px) {
        width: 1000px; }
    @media (max-width: 1024px) {
        width: 95%; } }






.app {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;

    .main {
        flex-grow: 1;
        display: flex;

        .content-wrapper {
            display: grid;
            padding-top: 100px;
            grid-template-columns: 1fr 5fr;
            grid-template-areas: 'n c';
            text-align: left;
            position: relative;
            grid-gap: 20px;
            height: 100%;
            flex-grow: 1;
            padding-bottom: 50px;

            @media (max-width: 700px) {
                display: flex; }


            .content {
                grid-area: c;
                position: relative;

                @media (max-width: 700px) {
                    flex-grow: 1; } } } } }







